.give-square-cc-fields {
  border: 1px solid #CCC;
  margin: 0;
  padding: .5em;
  box-sizing: border-box;
  width: 100%;
  border-radius: 0;
  outline: none;
  min-height: 45px;
}

.give-twentyseventeen {
  .give-square-cc-fields {
	min-height: 42px;
  }
}

.give-square-cc-fields--error {
  border: 1px solid red;
}



